import React, { Component } from 'react'
import Cloud from '../Cloud'
import FireWork from '../FireWork'
import Prompt from '../Prompt'
import gameLevel from '../../assets/LevelData/data'
import { createBox } from '../../controller/box'
import PlayChoose from '../PlayChoose'
import { createTortoise,bindTortoise } from "../../controller/tortoise"
import { Redirect } from 'react-router-dom'

import './index.css'

class Map extends Component {

    constructor(props) {
        super(props);
        this.state = { // level Ë°®Á§∫Ê∏∏ÊàèÂÖ≥Âç°, ÂàùÂßãÊòØÁ¨¨‰∏ÄÂÖ≥
            level: 0,
            success: false,
            process: 'ÈóØÂÖ≥ÊàêÂäü, Ëµ∂Âø´ËØïËØï‰∏ã‰∏ÄÂÖ≥Âêß!',
            midProcess: 'ÂÜçÊé•ÂÜçÂéâ, ËøòÂâ©ÊúÄÂêé‰∏ÄÂÖ≥ÂíØ!',
            result: 'ÊÅ≠Âñú‰Ω†Ëøá‰∫ÜÊúÄÈöæÁöÑ‰∏ÄÂÖ≥ÔºÅÂõûÈ¶ñÈ°µÁúãÁúãÂêß!',
            jump: false
        };
        this.playAgain = this.playAgain.bind(this);
        this.nextLevel = this.nextLevel.bind(this);
    }

    componentWillMount() {
        let queryStr = this.props.location.search.split('?')[1];
        if(queryStr) {
            console.log(queryStr.split('=')[1]);
            this.setState({
                level: parseInt(queryStr.split('=')[1]) // Âõ†‰∏∫Êü•ËØ¢‰∏≤Âèñ‰∏ãÁöÑlevelÊòØstringÁ±ªÂûãÁöÑ,ËøôÈáåË¶ÅËΩ¨‰∏∫numberÁ±ªÂûã,Âê¶ÂàôÂú®ÂàáÊç¢Âà∞‰∏ã‰∏ÄÂÖ≥Êó∂‰ºöÂõ†‰∏∫Â≠óÁ¨¶‰∏≤Áõ∏Âä†ËÄåÊâæ‰∏çÂà∞ÂØπÂ∫îÁöÑÊï∞ÊçÆÂá∫Èîô
            })
        }
        console.log(queryStr)
    }

    componentDidMount() {
        bindTortoise(gameLevel[this.state.level], this) // ÁªÑ‰ª∂ÊåÇÂú®ÂÆå‰πãÂêéËøõË°åkeydown‰∫ã‰ª∂ÁöÑÁªëÂÆö
    }

    componentDidUpdate() {
        console.log('Êõ¥Êñ∞ÂÆåÊàê')
       if(!this.state.success) {
           createTortoise(gameLevel[this.state.level], true); // Ê≥®ÊÑèËøôÊó∂ÂÄôÂõ†‰∏∫‰º†ÂÖ•‰∫Ütrue,ÊâÄ‰ª•‰∏ç‰ºöËøîÂõûjsx,ËÄåÊòØÂØπÂ∑≤ÁªèÊõ¥Êñ∞ÂÆåÊàêÁöÑtortoiseËøõË°åÂÆö‰ΩçÔºå‰∏ãÈù¢ÁöÑcreateBox‰πüÊòØÂêåÁêÜ
           createBox(gameLevel[this.state.level], true);
           bindTortoise(gameLevel[this.state.level], this) // Âú®ÁªÑ‰ª∂Êõ¥Êñ∞‰πãÂêéÈáçÊñ∞ÂØπÁªÑ‰ª∂ËøõË°å‰∫ã‰ª∂ÁªëÂÆö
       }
    }

    playAgain() {
        // playAgainÊúâ‰∏§ÁßçË∞ÉÁî®ÊÉÖÂÜµ 1.Âú®Ê∏∏ÊàèËøáÁ®ã‰∏≠,Âõ†‰∏∫Ëµ∞Èîô‰∏ÄÊ≠•ËÄåÊó†Ê≥ïÂÆåÊàê,Ë¶ÅplayAgain; 2.ËøôÂ±ÄÈÄöÂÖ≥‰∫Ü,ÊÉ≥ÂÜçÁé©‰∏ÄÊ¨°,Ë¶ÅplayAgain
        if(this.state.success) {
            this.setState((state)=>{
                return {
                    success: !state.success
                }
            })
        }else {
            this.setState((state)=>{ // Â±û‰∫éÁ¨¨‰∫åÁßçÁöÑplayAgain, ‰ªÖ‰ªÖÊÉ≥Ë¶ÅÊää‰πåÈæüÂíåboxÂ§ç‰ΩçËÄåÂ∑≤
                return {
                    success: state.success
                }
            })
        }
    }

    nextLevel() { // nextLevelÊúâ‰∏§ÁßçË∞ÉÁî®ÊÉÖÂÜµ 1.Âú®Ê∏∏ÊàèËøáÁ®ã‰∏≠,Âõ†‰∏∫ÂÆåÊàêËøôÂÖ≥Ë¶ÅËøõÂÖ•‰∏ã‰∏ÄÂÖ≥,Ë∞ÉÁî®playAgain; 2.ÊúÄÂêé‰∏ÄÂÖ≥ÂÆåÊàê‰∫Ü,Ë¶ÅË∑≥ËΩ¨Âà∞levelChooseÁªÑ‰ª∂,Ë∞ÉÁî®playAgain
        if(this.state.level === 2) {
            this.setState((state)=>{
                return {
                    jump: !state.jump
                }
            })
        }else {
            this.setState((state)=>{
                return {
                    level: state.level + 1,
                    success: !state.success
                }
            })
        }
    }
    render() {
        console.log('ÈáçÊñ∞Ê∏≤Êüì');
        let level = this.state.level;
        console.log(level);
        let levelData = gameLevel[level].map; // Ê†πÊçÆÂÖ≥Âç°Êï∞ÈÄâÂá∫ÂØπÂ∫îÊï∞ÊçÆ, mapÊòØ‰∏Ä‰∏™Êï∞ÁªÑ
        let sideLength = Math.sqrt(levelData.length) * 50;
        let dom = null;
        return (
            <div className="container">
                { this.state.jump ? <Redirect to={{ pathname: '/levelChoose' }} /> : ''}
                <Cloud />
                <PlayChoose playAgain={this.playAgain}/>
                <div id="map" style={{ width: sideLength}}>
                    {
                        levelData.map((val, index)=>{
                            switch(val) { // Ê†πÊçÆelemÁöÑÂÄºÊù•Â∫îÁî®Ê†∑Âºè
                                case 1 : dom = (<div className="cell" key={index}></div>); break; // ÊôÆÈÄöÊ†ºÂ≠ê
                                case 2 : dom = (<div className="wall" key={index}></div>); break;// Â¢ô
                                case 3 : dom = (<div className="target" key={index}></div>); break;// ÁõÆÊ†áÊ†ºÂ≠ê
                                default: break;
                            }
                            return dom;
                        })
                    }
                    { createBox(gameLevel[level])  }
                    { createTortoise(gameLevel[level]) }
                    { this.state.success && this.state.level === 2 ? (<FireWork />) : '' }
                    { this.state.success ?  <Prompt
                        playAgain={this.playAgain}
                        nextLevel={this.nextLevel}
                        title={this.state.level === 2 ? this.state.result : (this.state.level === 0 ? this.state.process : this.state.midProcess)}/> : '' }
                </div>
            </div>
        )
    }
}

export default Map;
