import React from 'react'
import $ from 'jquery'

/**
 * function : ÂàõÂª∫‰πåÈæü, Ê†πÊçÆÂΩìÂâçlevel‰∏≠ÁöÑtortoiseÂØπË±°ÁöÑx, yËÆæÁΩÆleftÂíåtopÂÄº, ËøõË°åÂÜ≥ÂÆöÂÆö‰Ωç
 * param : nowLevel ÂΩìÂâçÂÖ≥Âç°Êï∞ÊçÆ ; changePos ÂΩìÂÆÉ‰∏∫trueÊó∂,Ë°®Á§∫Ë¶ÅÂØπ‰πåÈæüËøõË°åÈáçÂÆö‰Ωç,Âú®Áî®Êà∑ÁÇπÂáªÂÜçÁé©‰∏ÄÊ¨°Êó∂‰º†ÂÖ•true
 * */
export function createTortoise(nowLevel, changePos) {
    console.log('ÂàõÂª∫‰πåÈæü')
    let left = nowLevel.tortoise.x * 50;
    let top = nowLevel.tortoise.y * 50;
    if(changePos) {
        $('.tortoise').css('left', left);
        $('.tortoise').css('top', top);
    }else return (
        <div className="tortoise" style={{ left, top }}></div>
    )
}

/**
 * function : ‰∏∫‰πåÈæüÁªëÂÆökeydown‰∫ã‰ª∂, Âπ∂Ê†πÊçÆÈîÆÁõòÁöÑ‰∏ä‰∏ãÂ∑¶Âè≥ÊîπÂèò‰πåÈæüÊúùÂêë
 * param : none
 * return : none
 * */
export function bindTortoise(nowLevel, _this) {
    $(document).unbind(); // Ëß£Èô§ÂÖàÂâçÁªëÂÆöÁöÑkeydown‰∫ã‰ª∂,Âê¶Âàô‰ºöÁßªÂä®‰ºöÂá∫Áé∞Ë∑≥Ê≠•ÁöÑÊÉÖÂÜµ
    let tortoise = $('.tortoise');
    tortoise.data('x', nowLevel.tortoise.x); // Â≠òÂÇ®‰πåÈæüÁöÑÂùêÊ†á, Âú®ÁßªÂä®ÁöÑÊó∂ÂÄô‰ΩøÁî®
    tortoise.data('y', nowLevel.tortoise.y);
    $(document).keydown(function(event) {
        switch(event.which) { //
            case 37 : // üëà
                tortoise.css('backgroundPosition', '-150px 0 '); // ÊîπÂèò‰πåÈæüÊúùÂêë
                walkTortoise.call(_this, tortoise, { x: -1 }, nowLevel); // ÊéßÂà∂‰πåÈæüËµ∞Âä®
                break;
            case 38 : // üëÜ
                tortoise.css('backgroundPosition', '0 0 ');
                walkTortoise.call(_this, tortoise, { y: -1 }, nowLevel);
                break;
            case 39 : // üëâ
                tortoise.css('backgroundPosition', '-50px 0 ');
                walkTortoise.call(_this, tortoise, { x: 1 }, nowLevel);
                break;
            case 40 : // üëá
                tortoise.css('backgroundPosition', '-100px 0 ');
                walkTortoise.call(_this, tortoise, { y: 1 }, nowLevel);
                break;
            default : break;
        }
    })
}

/**
 * function : ÊéßÂà∂‰πåÈæüÁßªÂä®
 * param: tortoise: ‰πåÈæü, walk: ÁßªÂä®Êó∂ÊâÄÂú®Ë°åÊàñËÄÖÂàóÁöÑÊîπÂèò, nowLevel : ÂΩìÂâçÂÖ≥Âç°Êï∞ÊçÆ
 * return none
 * */
function walkTortoise(tortoise, walk, nowLevel) { // ‰πåÈæüÁßªÂä®
    let walkX = walk.x || 0; // ‰º†‰∏ãÊù•ÁöÑwalk‰∏≠Âè™ÊúâxÊàñËÄÖÂè™Êúây, Â¶ÇÊûúÊ≤°ÊúâÁöÑÂÄºÂ∞±‰∏∫0, ÈÇ£‰πàÂ∞±‰∏ç‰ºöÂØπ‰ΩçÁΩÆ‰∫ßÁîüÂΩ±Âìç
    let walkY = walk.y || 0;
    let rowNum = Math.sqrt(nowLevel.map.length); // mapÊØèË°åÁöÑÊ†ºÂ≠ê‰∏™Êï∞
    let kind = nowLevel.map[(tortoise.data('y') + walkY) * rowNum + tortoise.data('x') + walkX];
    if(kind !== 2) {
        tortoise.data('y', tortoise.data('y') + walkY); // ËÆæÁΩÆ‰πåÈæüÁöÑÂùêÊ†á
        tortoise.data('x', tortoise.data('x') + walkX);

        tortoise.css('top', tortoise.data('y') * 50); // ÁßªÂä®‰πåÈæü
        tortoise.css('left', tortoise.data('x') * 50);

        $('.box').each($.proxy(function(i, elem) {
            if(impactCheck(tortoise, $(elem))) { // Â¶ÇÊûúÁ¢∞‰∏äbox‰∫Ü, ÈÇ£‰πàÂΩìÂâç‰πåÈæüÁöÑ‰ΩçÁΩÆÂíåÊüê‰∏™boxÁöÑ‰ΩçÁΩÆÊòØÁõ∏ÂêåÁöÑ, Êé•‰∏ãÊù•Â∞±ÁúãËøô‰∏™boxÊòØÂê¶ÂèØ‰ª•ÂæÄ‰πåÈæüÊù•ÁöÑÈÇ£‰∏™ÊñπÂêëÁßªÂä®
                kind = nowLevel.map[(tortoise.data('y') + walkY) * rowNum + tortoise.data('x') + walkX];
                if(kind !== 2) { // Â¶ÇÊûúÂíå‰πåÈæüÁ¢∞ÊíûÁöÑÈÇ£‰∏™ÁÆ±Â≠êÂèØ‰ª•ÂæÄÊåáÂÆöÊñπÂêëÁßªÂä®ÁöÑËØù, Â∞±ÊîπÂèòÁÆ±Â≠êÁöÑleftÂíåtop, Â∞±Áõ∏ÂΩì‰∫éÊääÂú®‰πåÈæüË∫´‰∏äÂπ≤ÂÅöËøáÁöÑ,Âú®boxË∫´‰∏äÂÜçÂÅö‰∏ÄÊ¨°, ËÆ©boxÁßªÂä®
                    $(elem).css('left', (tortoise.data('x') + walkX) * 50);
                    $(elem).css('top', (tortoise.data('y') + walkY) * 50);
                    $('.box').each($.proxy(function(j, Elem ){ // ÂØπÂàöÊâçÁßªÂä®ÁöÑÈÇ£‰∏™boxÂÜçÂÅö‰∏ÄÊ¨°Á¢∞ÊíûÊ£ÄÊµã(ËøôÈáåË¶ÅÊ≥®ÊÑèÂè™ÊúâÁúüÊ≠£ÁöÑÁßªÂä®‰∫ÜÊâçÂèØ‰ª•ÂÅöÁ¢∞ÊíûÊ£ÄÊµã),Â¶ÇÊûúÊúâÁ¢∞Êíû(ÊéíÈô§Ëá™Ë∫´ÁöÑÂπ≤Êâ∞),Â∞±Êí§ÂõûÂàöÊâçÂØπboxÁöÑÁßªÂä®
                        if(elem !== Elem && impactCheck($(elem), $(Elem))) {
                            $(elem).css('left', (tortoise.data('x')) * 50); // ÊääboxÁßªÂä®Âà∞‰πåÈæüÊâÄÂú®ÁöÑ‰ΩçÁΩÆ
                            $(elem).css('top', (tortoise.data('y')) * 50);

                            tortoise.data('y', tortoise.data('y') - walkY); // ÈáçÊñ∞ËÆæÁΩÆ‰πåÈæüÁöÑÂùêÊ†á
                            tortoise.data('x', tortoise.data('x') - walkX);

                            tortoise.css('top', tortoise.data('y') * 50);   // Êää‰πåÈæüÁßªÂä®Âà∞ÂéüÊù•ÁöÑ‰ΩçÁΩÆ
                            tortoise.css('left', tortoise.data('x') * 50);
                        }
                    }, this));

                }else { // Â¶ÇÊûúboxÊó†Ê≥ïÁßªÂä®Â∞±Êí§Âõû‰πåÈæüÁöÑÁßªÂä®
                    tortoise.data('y', tortoise.data('y') - walkY);
                    tortoise.data('x', tortoise.data('x') - walkX);

                    tortoise.css('top', tortoise.data('y') * 50);
                    tortoise.css('left', tortoise.data('x') * 50);
                }
            }else {

            }
        }, this));
    }

    // Ê£ÄÊµãÊòØÂê¶ËØ•ËøõÂÖ•‰∏ã‰∏ÄÂÖ≥
    if(nextLevel()) {
        $(document).unbind();
        this.setState((state)=>{
           return {
               success: !state.success
           }
        });
    }
}

/**
 * function : Á¢∞ÊíûÊ£ÄÊµã
 * param : elem1 ÂÖÉÁ¥†‰∏Ä, elem2 ÂÖÉÁ¥†‰∫å
 * return : none
 * */
function impactCheck(elem1, elem2) {
    let left1 = elem1.offset().left;
    let right1 = elem1.offset().left + elem1.width();
    let top1 = elem1.offset().top;
    let bottom1 = elem1.offset().top + elem1.height();

    let left2 = elem2.offset().left;
    let right2 = elem2.offset().left + elem2.width();
    let top2 = elem2.offset().top;
    let bottom2 = elem2.offset().top + elem2.height();

    return !(left1 >= right2 || right1 <= left2 || top1 >= bottom2 || bottom1 <= top2);
}

/**
 * function : Âà§Êñ≠ÊòØÂê¶ÊàêÂäü,ÊàêÂäü‰∫ÜÂ∞±ËøõÂÖ•‰∏ã‰∏ÄÂÖ≥
 *param : none
 * return : none
 * */
function nextLevel() {
    let numberOfCoincidences = 0;
    $('.target').each($.proxy(function(i, elem){ // Â¶ÇÊûúÁõÆÊ†á‰ΩçÁΩÆË¢´boxÂÖ®ÈÉ®Âç†Êª°Â∞±ÈÄöÂÖ≥ÊàêÂäü
        $('.box').each($.proxy(function(j, Elem){
            if($(elem).offset().top === $(Elem).offset().top && $(elem).offset().left === $(Elem).offset().left) {
                numberOfCoincidences ++;
            }
        }, this));
    }, this));
    if(numberOfCoincidences === $('.target').length) {
        return true;
    }else return false;
}
