import React from 'react'
import $ from 'jquery'

/**
 * function : ÂàõÂª∫ÁÆ±Â≠ê, Ê†πÊçÆÂΩìÂâçlevel‰∏≠boxÂØπË±°ÁöÑx, y‰∏∫boxËÆæÁΩÆleftÂíåtopÂÄº, ËøõË°åÁªùÂØπÂÆö‰Ωç
 * param : levelData ÂÖ≥Âç°ÁöÑÊï∞ÊçÆ; changePosË°®Á§∫Ë¶ÅÂØπboxÈáçÊñ∞ËøõË°åÂÆö‰Ωç, Ëøô‰∏™ÂèÇÊï∞Âú®Áî®Êà∑ÁÇπÂáªÂÜçÁé©‰∏ÄÊ¨°Êó∂‰ºöË¢´‰º†ÂÖ•true
 * ËøôÈáå‰πãÊâÄ‰ª•Ë¶ÅÂºïÂÖ•changePosÁöÑÂéüÂõ†ÊòØÂõ†‰∏∫reactÁöÑdifferÁÆóÊ≥ïÔºåÊòØÁöÑÂú®Ê∏∏ÊàèËÉúÂà©‰πãÂêéÔºåÊàëËÆæÁΩÆstateÁöÑsuccess‰∏∫true‰πãÂêéÔºåËôΩÁÑ∂ÁªÑ‰ª∂ÊâßË°å‰∫ÜrenderÂáΩÊï∞
 * ‰ΩÜÊòØÁî±‰∫é‰πåÈæüÂíåboxÁöÑÊñ∞Êóßvirtual domÊòØ‰∏ÄÊ†∑ÁöÑÔºåÂØºËá¥‰πåÈæüÂíåbox‰∏çËÉΩËá™Âä®Â§ç‰Ωç,ÊâÄ‰ª•‰∏∫‰∫ÜÂú®Áî®Êà∑ÁÇπÂáªÂÜçÁé©‰∏ÄÊ¨°ÁöÑÊó∂ÂÄôÂÆÉ‰ª¨ËÉΩÂ§ç‰Ωç,Â∞±Áî®changePosÂ±ûÊÄßÊù•Âà§Êñ≠Êìç‰ΩúÁ±ªÂûã: ÂÆö‰Ωç or ÂàùÂßãÂåñ, createTortoiseÊñπÊ≥ï‰πüÂêåÁêÜ
 * return : none
 * */
export function createBox (levelData, changePos) {
    let box = levelData.box;
    let len = levelData.map.length;
    if(changePos) {
        $('.box').each((i, elem)=>{
            $(elem).css('left', box[i].x * 50);
            $(elem).css('top', box[i].y * 50);
        })
    }else return box.map((val, index)=>{
        let left = val.x * 50;
        let top = val.y * 50;
        return (
            <div className="box" style={{left, top}} key={len + index}></div>
        )
    });
}
